  border: 1px solid #ff5412;
  color: #fff;
  padding: 13px 46px 13px 24px;
  transition: background-color 150ms ease-out, background-image 150ms ease-out,
    background-position 150ms ease-out, color 150ms ease-out;
}
.style-solid.btn .el.orange:focus:not([disabled]),
.style-solid.btn .el.orange:hover:not([disabled]) {
  background-position: calc(100% - 24px) 20px;
