.features-hero .feature-hero-boxes {
  display: flex;
  flex-direction: row;
  margin-top: 8px;
  margin-bottom: 24px;
  justify-content: flex-start;
  width: 100%;
}
@media (min-width: 24em) {
  .features-hero .feature-hero-boxes {
    justify-content: center;
  }
}
.features-hero .feature-hero-boxes-box {
  display: flex;
  position: relative;
  justify-content: center;
  margin-right: 20px;
  align-items: center;
  flex-direction: column;
  text-decoration: none;
  margin-bottom: 16px;
  width: 40px;
  height: 40px;
}
.features-hero .image-icon {
  height: 100%;
}
.features-hero .box-text {
  display: none;
  color: #fff;
  max-width: 100%;
  text-align: center;
  text-transform: uppercase;
  word-wrap: break-word;
  margin-bottom: 6px;
  margin-top: 6px;
  font-family: "Roboto Mono", "Courier New", monospace;
  font-weight: 500;
  transition: 150ms font-size, 150ms line-height;
  transition: none;
  font-size: 12px;
  line-height: 20px;
}
.features-hero .box-text::before {
  display: none;
}
@media (min-width: 35.5em) {
  .features-hero .box-text {
    margin-bottom: 8px;
    margin-top: 8px;
    font-size: 14px;
    line-height: 24px;
  }
}
.features-hero .box-arrow {
  display: none;
  background: 0 0;
  height: 14px;
  position: absolute;
  top: 0;
  transition: top 150ms;
  width: 30px;
  z-index: 3;
  right: 0;
}
.features-hero .box-arrow::after,
.features-hero .box-arrow::before {
  border-color: transparent;
  border-style: solid;
  content: "";
  position: absolute;
  pointer-events: none;
}
.features-hero .box-arrow::before {
  bottom: 0;
  right: 9px;
  border-left-width: 6px;
  border-top-color: #fff;
  border-top-width: 6px;
  border-right-width: 6px;
}
.features-hero .box-arrow::after {
  bottom: 2px;
  right: 11px;
  border-left-width: 4px;
  border-top-color: teal;
  border-top-width: 4px;
  border-right-width: 4px;
}
@media (min-width: 48em) {
  .features-hero .feature-hero-boxes {
    justify-content: center;
    margin-top: 40px;
    margin-bottom: 48px;
  }
  .features-hero .feature-hero-boxes-box {
    border: 1px solid #ddd;
    height: auto;
    min-height: 142px;
    padding: 16px 20px;
    margin-bottom: 0;
    width: 100%;
    transition: all 150ms;
  }
  .features-hero .feature-hero-boxes-box:last-child {
    margin-right: 0;
  }
  .features-hero .feature-hero-boxes-box:hover {
    background: rgba(51, 150, 172, 0.3);
    border: 1px solid #fff;
  }
  .features-hero .feature-hero-boxes-box:hover .box-arrow {
    top: 4px;
  }
  .features-hero .image-icon {
    width: auto;
    height: auto;
  }
  .features-hero .box-text {
    display: block;
    letter-spacing: 0.5px;
    color: #fff;
    margin-bottom: 6px;
    margin-top: 6px;
    font-family: "Roboto Mono", "Courier New", monospace;
    font-weight: 500;
    transition: 150ms font-size, 150ms line-height;
    transition: none;
    font-size: 12px;
    line-height: 20px;
  }
  .features-hero .box-text::before {
    display: none;
  }
  .features-hero .box-arrow {
    display: block;
    background: 0 0;
    height: 14px;
    position: relative;
  }
}
@media (min-width: 48em) and (min-width: 35.5em) {
  .features-hero .box-text {
    margin-bottom: 8px;
    margin-top: 8px;
    font-size: 14px;
    line-height: 24px;
  }
}
@media (min-width: 64em) {
  .features-hero .feature-hero-boxes {
    justify-content: space-between;
  }
  .features-hero .feature-hero-boxes-box {
    margin-right: 40px;
  }
  .features-hero .box-text {
    letter-spacing: 1.6px;
  }
}
