a,
div,
header,
li,
nav,
ul {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
header,
nav {
  display: block;
}
ul {
  list-style: none;
}
*,
:after,
:before {
  box-sizing: inherit;
}
a {
  color: #ff5412;
