.std-text-image .title {
  margin-bottom: 8px;
  margin-top: 8px;
  font-family: "Source Sans Pro", Arial, sans-serif;
  font-weight: 500;
  transition: 150ms font-size, 150ms line-height;
  transition: none;
  font-size: 24px;
  line-height: 32px;
}
@media (min-width: 35.5em) {
  .std-text-image .title {
    margin-bottom: 12px;
    margin-top: 12px;
  }
}
.std-text-image .title::before {
  content: "";
  display: block;
  margin: 48px 0 20px;
}
