/* Dashlane Footer bottam bar */

/*! CSS Used from: Embedded */
a,div,footer,li,p,span,ul{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline;}
footer{display:block;}
ul{list-style:none;}
*,:after,:before{box-sizing:inherit;}
p{margin-bottom:6px;margin-top:6px;font-family:'Source Sans Pro',Arial,sans-serif;font-weight:300;transition:150ms font-size,150ms line-height;transition:none;font-size:16px;line-height:24px;letter-spacing:.5px;}
p::before{display:none;}
a{color:#ff5412;transition:color 150ms;text-decoration:none;}
a:visited{color:#ff5412;}
a:active,a:hover{text-decoration:underline;color:#e44609;}
@media (min-width:35.5em){
p{margin-bottom:8px;margin-top:8px;font-size:18px;line-height:24px;}
}
.footer-std{border-top:none;display:flex;justify-content:center;}
.footer-std.footer-mentions{background:#e7e7e7;}
.footer-std.footer-mentions .horizontal-box{align-items:flex-start;flex-direction:column-reverse;}
@media (min-width:64em){
.footer-std.footer-mentions .horizontal-box{align-items:flex-end;flex-direction:row;}
}
.footer-std .inner{display:flex;flex-direction:column;align-items:stretch;justify-content:flex-start;padding:0;margin:0 auto;position:relative;min-width:320px;width:100%;max-width:1280px;min-height:112px;justify-content:center;}
@media (min-width:80em){
.footer-std .inner{padding-left:40px;padding-right:40px;margin:0 -40px;width:1360px;max-width:inherit;}
}
.footer-std .horizontal-box{display:flex;justify-content:space-between;flex-direction:row;align-items:flex-start;padding:16px 20px;}
@media (min-width:24em){
.footer-std .horizontal-box{padding-left:40px;padding-right:40px;}
}
.footer-std [class^=column]{width:100%;}
.footer-std [class^=column] ul{margin:0;padding:0;}
.footer-std [class^=list-item]{font-size:14px;line-height:30px;color:#474747;list-style:none;margin:0;padding:0;}
.footer-std .list-item-head{font-family:'Roboto Mono','Courier New',monospace;text-transform:uppercase;font-weight:600;letter-spacing:1px;}
.footer-std .desktop{display:none;}
@media (min-width:64em){
.footer-std [class^=column]{width:20%;flex-grow:1;}
.footer-std .column-double{width:40%;flex-grow:2;}
.footer-std .desktop{display:flex;}
}
.footer-std .desktop .list-item-head{margin-top:20px;}
.footer-std .desktop .list-link{color:#474747;text-decoration:none;}
.footer-std .desktop .list-link:hover{text-decoration:underline;}
.footer-std .mobile{display:flex;flex-direction:column;}
@media (min-width:35.5em){
.footer-std .mobile{padding-right:0;}
}
@media (min-width:48em){
.footer-std .mobile{padding-right:0;}
}
.footer-std .mobile select{border:none;font-size:16px;height:50px;left:0;opacity:0;position:absolute;top:0;width:100%;}
.footer-std .locale-and-legal{margin-bottom:16px;}
@media (min-width:64em){
.footer-std .mobile{display:none;}
.footer-std .locale-and-legal{text-align:right;margin-bottom:0;}
}
.footer-std .caption{font-size:14px;line-height:24px;font-weight:500;color:#474747;margin:0;padding:6px 0;}
.footer-std .micro{font-size:12px;line-height:18px;color:#474747;margin:0;padding:0;}
.footer-std .micro .sep::before{content:' | ';padding:0 4px;}
.footer-std .micro a{cursor:pointer;color:#474747;}
.footer-std .language-container{position:relative;}
.footer-std .language-selector{width:100%;padding:9px 0;z-index:1;}
.footer-std .language-selector::before{content:'';display:inline-block;width:18px;height:18px;margin:0 8px -5px 0;background:url(https://d38muu3h4xeqr1.cloudfront.net/website/static/WN-1548/images/footer/language-selector-icon.svg) center center no-repeat;}
.footer-std .language-selector::after{content:'';display:inline-block;width:9px;height:9px;margin:0 0 0 8px;background:url(https://d38muu3h4xeqr1.cloudfront.net/website/static/WN-1548/images/footer/language-selector-triangle.svg) center center no-repeat;transition:transform 150ms;}
.footer-std ul.language-dropdown{display:none;z-index:0;position:absolute;right:-20px;bottom:0;background:#e7e7e7;border:1px solid #dbdbdb;text-align:left;margin:0;padding:10px 20px 30px;}
.footer-std ul.language-dropdown .list-item-head{margin-top:0;margin-right:20px;}
.footer-std ul.language-dropdown .list-item-page a{display:block;}
.footer-std ul.language-dropdown .list-item-page.selected a{font-weight:500;color:#939393;background:url(https://d38muu3h4xeqr1.cloudfront.net/website/static/WN-1548/images/footer/language-selected-check.svg) right center no-repeat;}
.footer-std select.language-dropdown-select{height:30px;z-index:2;}
